/* css */
import React from "react";
import ReactDOM from "react-dom";
import "./index.css";
import App from "./App";
/**
 *  antd.css ( 2.5M)
 *  onemind  ant.css 
 *  webpack.IgnorePlugin  css webpack 
 */
if (process.env.NODE_ENV !== "production") {
  require("antd/dist/antd.css");
  // require("./jsencrypt");
  // require("./index.css");
  const { isLogin, login } = require("./api/asset");
  const account = {
    user: "admin",
    password: "sdy_23sZG",
  };
  //  customConfig 
  let customConfig = {
    title: "",
    desc: "",
    url: "https://www.sdata1010.cn/",
    imgUrl:
      "https://www.baidu.com/img/PCtm_d9c8750bed0b3c7d089fa7d55720d6cf.png",
  };

  isLogin()
    .then((res) => {
      if (!res.data) {
        const PUBLIC_KEY =
          "MFwwDQYJKoZIhvcNAQEBBQADSwAwSAJBANNmSJW87EE2Z3KDW5Kod8cL + 7lUBgfKLm86CGfMQxvc8w + JnOE7GV72DVyg2kCMGho5g9AR64BmrGobbG4xMZECAwEAAQ ==";
        const Encrypt = (text) => {
          if (!text) {
            return;
          }
          const encrypt = new window.JSEncrypt();
          encrypt.setPublicKey(
            "-----BEGIN PUBLIC KEY-----" +
              PUBLIC_KEY +
              "-----END PUBLIC KEY-----"
          );
          const encrypted = encrypt.encrypt(text);
          return encrypted.toString();
        };
        login({
          account: account.user,
          username: account.user,
          password: Encrypt(account.password),
        })
          .then((res) => {
            window.token = res.data;
          })
          .catch((err) => {
            console.log(err);
            console.error("");
          })
          .finally(() => {
            ReactDOM.render(
              <App customConfig={customConfig} />,
              document.getElementById("root")
            );
          });
      } else {
        ReactDOM.render(
          <App customConfig={customConfig} />,
          document.getElementById("root")
        );
      }
    })
    .catch((err) => {
      console.log("err", err);
      ReactDOM.render(
        <App customConfig={customConfig} />,
        document.getElementById("root")
      );
    });
} else {
  if (!window.CUSTOM_PLUGIN) {
    window.CUSTOM_PLUGIN = new Map();
  }

  window.CUSTOM_PLUGIN.set(process.env.CUSTOM_PLUGIN_ID, (dom, props) => {
    ReactDOM.render(<App {...props} />, dom);
  });
}
